@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 75px 0;
  font-size: 10px;
}

*,
::before,
::after {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  display: flex;
  justify-content: center;
  width: 90%;
  margin: 0 auto;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  margin: 0 auto;
}


.Table {
  border: 1px solid rgba(88, 88, 90, 0.1);
  text-align: left;
  border-radius: 15px;
  padding: 10px 0;
  box-shadow: 0px 6px 19px -8px rgba(88, 88, 90, 1);
}

table {
  border-collapse: collapse;
  margin: 0 auto;
}


th {
  color: rgba(88, 88, 90, 0.5);
  font-family: 'Lato', sans-serif;
  font-size: .8rem;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}

td {
  border: 1px solid rgba(88, 88, 90, 0.085);
  color: #000000;
  font-family: 'Lato', sans-serif;
  font-size: .8rem;
  font-weight: 400;
  text-align: left;
}

td:first-child {
  border-left: 0;
}

td:last-child {
  border-right: 0;
}

th,
td {
  padding: 3px 10px;
}

input[type='checkbox'].columnBox {
  -webkit-appearance: none;
  width: 25px;
  height: 20px;
  background: white;
  border-radius: 3px;
  border: 1px solid rgb(176, 168, 168);
  outline: none;
}


.orange {
  color: #f4a624;
  text-align: center;
  margin: 0 auto;
}

.blue {
  color: #0043ff;
}

.fas {
  font-size: 1.25rem;
}

.fa-arrow-circle-right {
  color: #51E3C1;
  font-size: 1.5rem;
}

.fa-exclamation-circle {
  color: #f4a624;
  font-size: 1.5rem;
}

.fa-check-circle {
  color: #0043ff;
  font-size: 1.5rem;
}

.merchant,
.teammember {
  font-weight: 700;
  color: white;
  background: orange;
  border-radius: 50%;
  height: 20px;
  padding: 13px;
  width: 20px;
  text-align: left;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-right: 5px;
}

.teammember {
  background-color: #28147b;
}

.pagination-container {
  margin: 30px auto;
}

.pagination-container .page-link {
  font-size: 1.2rem;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  color: #000;
  cursor: pointer;
  outline: none;
}

.pagination>.page-item {
  padding: 0;
}

.pagination-container .page-link:hover {
  color: #0043ff;
}
.form-group {
  align-self: flex-start;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
  flex-direction: row !important;
}

input[type="text"].form-control {
  color: #58585a;
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  align-self: flex-start;
  width: 300px;
  margin-left: auto;
}

.has-search .form-control {
  padding-left: 2.375rem;
  border-radius: 10px;
  margin-right: auto;
  box-shadow: 0px 6px 19px -8px rgba(88, 88, 90, 1);
}

.has-search .form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  font-size: .85rem;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  pointer-events: none;
  color: #aaa;
}

.filterButton {
  outline: none !important;
  cursor: pointer;
  width: 15rem;
  height: 40px;
  text-align: left;
  color: #58585a;
  padding: 0 20px;
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  background: transparent !important;
  margin-left: 25px;
  box-shadow: 0px 6px 19px -8px rgba(88, 88, 90, 1);
}

.dropdown-menu {
  width: 15rem;
}

ul {
  list-style-type: none;
}

ul>li {
  text-align: left;
  color: #58585a;
  padding: 0 20px;
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
}
